import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import zIndex from '@material-ui/core/styles/zIndex';

const useStyles = makeStyles((theme) => ({
    paper: {
        position: 'relative',
        border: '1px solid',
        borderRadius: "20px",
        maxWidth: "50%",
        margin: '10px',
        padding: '10px 20px',
        color:'#444444',
        backgroundColor:'#F7F9FC'
    },
  }));

export default function TransitionsPopper() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(anchorEl ? null : event.currentTarget);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'transitions-popper' : undefined;

  const popperContent = <div style={{"margin": "10px"}}>
      <p>
          This app aims at analyzing a MLP regressor predicting the selling 
          price of BMW cars from cars characteristics with <a href="https://github.com/slundberg/shap" target="_blank">Shap</a> values.
          The dataset consists of second hands BMW cars with features like mileage, year, power... 
        </p>
        <p>The <b>Dataset Exploration</b> panel is a visualization build with <a href="https://reactjs.org/" target="_blank">React</a> and <a href="https://d3js.org/">D3js</a></p>
        <p>The <b>Predictions</b> panel get the model predicted price for specific car features values. 
        The prediction is explained with the kernel SHAP algorithm (see <a href="https://arxiv.org/pdf/1705.07874.pdf" target="_blank">Paper</a>)

        </p>
    </div>

  return (
    <div>
    <div style={{"textAlign": "left", "margin": "10px"}}>
    <Button
            variant="contained"
            color="primary"
            startIcon={<Icon>send</Icon>}
            onClick={handleClick}
            >App description
    </Button>
    </div>
      <Popper 
        id={id} 
        open={open} 
        anchorEl={anchorEl} 
        placement="bottom-end" 
        modifiers={{
            flip: {
              enabled: false,
            }
          }}
        transition
        >
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <div className={classes.paper}>{popperContent}</div>
          </Fade>
        )}
      </Popper>
    </div>
  );
}